/* eslint-disable no-shadow */
import AFRAME from 'aframe';

const state = {
  isPlaying: true,
  time: 0,
  score: 0,
  health: 0,
  activePowerups: [],
};

const getters = {
  isPlaying: ({ isPlaying }) => isPlaying,
  time: ({ time }) => time,
  score: ({ score }) => score,
  health: ({ health }) => health,
  activePowerups: ({ activePowerups }) => activePowerups,
};

const mutations = {
  setIsPlaying(state, isPlaying) {
    state.isPlaying = isPlaying;
  },
  setTime(state, time) {
    state.time = time;
  },
  setHealth(state, health) {
    state.health = health;
  },
  incrementScore(state, score) {
    state.score += score;
  },
  incrementHealth(state, health) {
    state.health += health;
  },
  decrementHealth(state, damage) {
    state.health -= damage;
  },
  addPowerup(state, powerup) {
    state.activePowerups.push(powerup);
  },
  removePowerup(state, powerup) {
    state.activePowerups = state.activePowerups.filter((item) => item !== powerup);
  },
};

const actions = {
  setIsPlaying({ commit }, isPlaying) {
    commit('setIsPlaying', isPlaying);
  },
  play({ commit }) {
    commit('setIsPlaying', true);

    AFRAME.scenes[0].play();
  },
  pause({ commit }) {
    commit('setIsPlaying', false);

    AFRAME.scenes[0].pause();
  },
  setTime({ commit }, time) {
    commit('setTime', time);
  },
  setHealth({ commit }, health) {
    commit('setHealth', health);
  },
  incrementScore({ commit }, score) {
    commit('incrementScore', score);
  },
  incrementHealth({ commit }, health) {
    commit('incrementHealth', health);
  },
  decrementHealth({ commit, dispatch, state }, damage) {
    if (state.health - damage <= 0) {
      dispatch('finishGame');
    } else {
      commit('decrementHealth', damage);
    }
  },
  addPowerup({ commit }, powerup) {
    commit('addPowerup', powerup);
  },
  removePowerup({ commit }, powerup) {
    commit('removePowerup', powerup);
  },
  finishGame() {
    console.log('Game ended');
  },
};

export default {
  namespaced: true,
  actions,
  getters,
  mutations,
  state,
};
