<template>
  <button :class="classes" @click="click">
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'VButton',
  props: {
    fab: Boolean,
    outline: Boolean,
    to: String,
  },
  computed: {
    classes() {
      return {
        button: true,
        'button--fab': this.fab,
        'button--outline': this.outline,
      };
    },
  },
  methods: {
    click(event) {
      if (this.to) {
        this.$router.push({ path: this.to });
      }

      this.$emit('click', event);
    },
  },
};
</script>

<style lang="scss" scoped>
$background-color: #f1f1f1;
$border-color: #000;

.button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $background-color;
  border: 1px solid $border-color;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  font-size: 1rem;
  border-radius: 15px;
  padding: 0.8rem 1.2rem;

  &:hover {
    background-color: darken($color: $background-color, $amount: 10);
  }

  &:active {
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
                0 8px 10px 1px rgba(0, 0, 0, 0.14),
                0 3px 14px 2px rgba(0, 0, 0, 0.12);
  }
}

.button--fab {
  border-radius: 50%;
  padding: 0.5rem;
}

.button--outline {
  position: relative;
  background-color: transparent;
  border-radius: 20px;
  padding: 0.5rem;

  svg {
    position: absolute;
    top: 50%;
    left: 3px;
    transform: translateY(-50%);
  }
}
</style>
