/* eslint-disable no-shadow */
const state = {
  game: {},
  games: [],
};

const getters = {
  game: ({ game }) => game,
  games: ({ games }) => games,
};

const mutations = {
  setGame(state, game) {
    state.game = game;
  },
  setGames(state, games) {
    state.games = games;
  },
};

const actions = {
  setGame({ commit }, game) {
    commit('setGame', game);
  },
  setGames({ commit }, games) {
    commit('setGames', games);
  },
};

export default {
  namespaced: true,
  actions,
  getters,
  mutations,
  state,
};
