/* eslint-disable no-shadow */
const state = {
  token: '',
  name: '',
  wallet: {},
  vouchers: [],
  notifications: [],
};

const getters = {
  token: ({ token }) => token,
  name: ({ name }) => name,
  wallet: ({ wallet }) => wallet,
  vouchers: ({ vouchers }) => vouchers,
  notifications: ({ notifications }) => notifications,
};

const mutations = {
  setToken(state, token) {
    state.token = token;
  },
  setName(state, name) {
    state.name = name;
  },
  setWallet(state, wallet) {
    state.wallet = wallet;
  },
  setVouchers(state, vouchers) {
    state.vouchers = vouchers;
  },
  setNotifications(state, notifications) {
    state.notifications = notifications;
  },
};

const actions = {
  setUser({ commit }, user) {
    commit('setToken', user.token);
    commit('setName', user.name);
    commit('setWallet', user.wallet);
    commit('setVouchers', user.vouchers);
    commit('setNotifications', user.notifications);
  },
};

export default {
  namespaced: true,
  actions,
  getters,
  mutations,
  state,
};
