<template>
  <div class="home">
    <v-button fab class="home__exit">
      <exit-app-icon/>
    </v-button>

    <v-button fab class="home__options">
      <cog-icon/>
    </v-button>

    <v-button fab class="home__howToPlay">
      <help-icon/>
    </v-button>

    <h1 class="home__title">AR Shooter</h1>

    <div class="home__guns">
      <v-spinner v-if="isLoading"/>
      <v-slider :slides="slides" @select="selectGameConfig"/>
    </div>

    <div class="home__actions">
      <v-button to="/reward">Reward</v-button>
      <v-button @click="play">Play</v-button>
    </div>

    <v-modal v-if="showWarning" class="home__warning">
      <div class="home__warning__title">
        Wanna win rewards?
      </div>

      <p class="home__warning__text">
        Login to explore everything
      </p>

      <div class="home__warning__actions">
        <v-button to="/login">Login</v-button>
        <v-button to="/game">Continue anyway</v-button>
      </div>
    </v-modal>
  </div>
</template>

<script>
import ExitAppIcon from '@/components/common/icons/ExitApp.vue';
import CogIcon from '@/components/common/icons/Cog.vue';
import HelpIcon from '@/components/common/icons/Help.vue';
import VButton from '@/components/common/VButton.vue';
import VSlider from '@/components/common/VSlider.vue';
import VModal from '@/components/common/VModal.vue';
import VSpinner from '@/components/common/VSpinner.vue';
import { getGameConfigs } from '@/services/api';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'Home',
  components: {
    ExitAppIcon,
    CogIcon,
    HelpIcon,
    VButton,
    VSlider,
    VModal,
    VSpinner,
  },
  data() {
    return {
      isLoading: true,
      showWarning: false,
      selectedGameConfig: 0,
      gameConfigs: [],
      slides: [],
    };
  },
  computed: {
    ...mapGetters('config', [
      'games',
    ]),
  },
  methods: {
    ...mapActions('config', [
      'setGame',
      'setGames',
    ]),
    async getGameConfigs() {
      try {
        const res = await getGameConfigs();

        this.isLoading = false;
        this.gameConfigs = res.data;

        this.setGames(res.data);
        this.setSlides();
      } catch (error) {
        console.log(error);
      }
    },
    play() {
      const gameConfig = this.gameConfigs[this.selectedGameConfig];
      this.showWarning = true;

      this.setGame(gameConfig);
    },
    setSlides() {
      this.gameConfigs.forEach((gameConfig) => {
        this.slides.push({
          image: gameConfig.ui.gunImage,
        });
      });
    },
    setGameConfigs() {
      this.isLoading = false;
      this.gameConfigs = this.games;
      this.setSlides();
    },
    selectGameConfig(gameConfigIndex) {
      this.selectedGameConfig = gameConfigIndex;
    },
  },
  mounted() {
    if (this.games.length > 0) {
      this.setGameConfigs();
    } else {
      this.getGameConfigs();
    }
  },
};
</script>

