const { VUE_APP_API } = process.env;

export const getGameConfigs = async () => {
  const res = await fetch(`${VUE_APP_API}/game-configs/active`, {
    method: 'GET',
    headers: {
      Accept: 'application/json',
    },
  });

  return res.json();
};

export const login = async ({
  email,
  password,
  longitude,
  latitude,
}) => {
  const res = await fetch(`${VUE_APP_API}/login`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      email,
      password,
      longitude,
      latitude,
    }),
  });

  return res.json();
};

export const signup = async ({
  name,
  email,
  password,
  longitude,
  latitude,
}) => {
  const res = await fetch(`${VUE_APP_API}/register`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      name,
      email,
      password,
      longitude,
      latitude,
    }),
  });

  return res.json();
};

export default {
  getGameConfigs,
  login,
  signup,
};
