<template>
  <div class="slider">
    <v-button class="slider__next" fab @click="moveRight">
      <arrow-right-icon/>
    </v-button>

    <v-button class="slider__prev" fab @click="moveLeft">
      <arrow-left-icon/>
    </v-button>

    <ul class="slider__list" ref="list">
      <li
        class="slider__list__item"
        v-for="(slide, index) in slides"
        :key="index"
        ref="slide"
      >
        <img :src="slide.image">
      </li>
    </ul>
  </div>
</template>

<script>
import VButton from './VButton.vue';
import ArrowRightIcon from './icons/ArrowRight.vue';
import ArrowLeftIcon from './icons/ArrowLeft.vue';

export default {
  name: 'VSlider',
  components: {
    ArrowRightIcon,
    ArrowLeftIcon,
    VButton,
  },
  props: {
    slides: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selected: 0,
    };
  },
  computed: {
    firstSlideWidth() {
      return this.$refs.slide[0].offsetWidth;
    },
    widthLimit() {
      return (this.slides.length - 1) * this.firstSlideWidth;
    },
  },
  methods: {
    moveRight() {
      const { list } = this.$refs;
      const right = parseInt(list.style.right || 0, 10) + this.firstSlideWidth;

      if (right <= this.widthLimit) {
        list.style.right = `${right}px`;
        this.selected += 1;
      } else {
        list.style.right = '0px';
        this.selected = 0;
      }

      this.$emit('select', this.selected);
    },
    moveLeft() {
      const { list } = this.$refs;
      const right = parseInt(list.style.right || 0, 10) - this.firstSlideWidth;

      if (right >= 0) {
        list.style.right = `${right}px`;
        this.selected -= 1;
      } else {
        list.style.right = `${this.widthLimit}px`;
        this.selected = this.slides.length - 1;
      }

      this.$emit('select', this.selected);
    },
  },
};
</script>

<style lang="scss" scoped>
.slider {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  max-width: 400px;
  max-height: 200px;
}

.slider__list {
  position: relative;
  display: flex;
  margin: 0;
  padding: 0;
  right: 0;
  list-style: none;
  transition: right 1s;
}

.slider__list__item {
  position: relative;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  height: 200px;

  img {
    max-width: 100%;
    max-height: 100%;
  }
}

.slider__prev, .slider__next {
  position: absolute;
  top: 40%;
  z-index: 1;
}

.slider__prev {
  left: 5px;
}

.slider__next {
  right: 5px;
}
</style>
