import Vue from 'vue';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';

Vue.config.productionTip = false;

Vue.config.ignoredElements = [
  'a-scene',
  'a-assets',
  'a-asset-item',
  'a-entity',
  'a-camera',
];

window.addEventListener('load', () => {
  new Vue({
    router,
    store,
    render: (h) => h(App),
  }).$mount('#app');
});
